html {
  box-sizing: border-box;
}
*,*:before, *:after {
  box-sizing: inherit;
}

.container {
  background-image: url("https://i.ytimg.com/vi/o6wMCkY7kjA/maxresdefault.jpg");
  background-size: cover;
  width: 50%;
  border: 5px solid #5a8fe6;
  box-shadow: 8px 8px #436499;
  text-align: center;
  margin: 20px auto;
}

.form {
  margin: 5px;
  padding: 10px;
}

.form input {
  margin: 5px;
  text-align: center;
  border-radius: 5px;
}

.weather {
  margin: 3px;
  background-color: #5d697a;
}

